.nav {
    display: flex;
    flex-direction: column;
    background: $grayBackground;
    padding: 40px 0;

    @media (min-width: 992px) {
        flex-direction: row;
        background: none;
    }
}

.nav {
    .nav__list--mobile {
        @media (min-width: 992px) {
            display: none;
        }
    }
}

.nav__panel {
    display: flex;
    justify-content: space-between;
    margin-right: auto;

    @media (min-width: 992px) {
        justify-content: flex-start;
    }
}

.nav__list {
    top: 63px;
    left: 0;

    width: 100%;
    height: 454px;

    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    background: #fafafa;

    @media (min-width: 992px) {
        display: flex;
        height: initial;
        flex-direction: row;
        justify-content: flex-start;
        margin-left: 117px;

        background: initial;
    }
}


.nav__item {
    cursor: pointer;
    margin-bottom: 30px;
    a {
        font-family: Montserrat;
        font-weight: normal;
        font-size: 30px;
        line-height: 45px;
        text-align: center;
        letter-spacing: 0.2px;
    
        color: $secondText;

        transition: color 0.2s;

        @media (min-width: 992px) {
            margin-right: 20px;
            margin-bottom: 0;
    
            font-size: 14px;
            line-height: normal;
        }


        &:hover,
        &:focus {
            text-decoration: underline;
            color: darken($color: $secondText, $amount: 10%);
        }
    }

    @media (min-width: 992px) {
        margin-bottom: 0;
    }
}

.nav__toggler {
    margin-left: 22px;
    margin-top: 3px;

    font-size: 20px;

    background: none;
    color: #737373;
    border: none;
    cursor: pointer;
}

.nav__icons {
    display: flex;
    align-items: center;
    justify-content: center;

    @media (min-width: 992px) {
        display: none;
    }
}

.nav__icon {
    width: 24px;
    height: 24px;

    cursor: pointer;
}

.nav__icon:nth-child(1) {
    width: 20px;
    height: 20px;

    margin-right: 24px;
}

.nav__icon svg {
    width: 100%;
    height: 100%;
    fill: $secondText;
}