.features {
    padding-top: 98px;
    padding-bottom: 70px;

    background: $darkGrayBackground;

    @media (min-width: 1200px) {
        padding-top: 192px;
    }
}

.features--advice {
    padding-bottom: 96px;

    @media (min-width: 1200px) {
        padding-top: 125px;
    }

    .feautres__title {
        text-transform: none;
    }
}

.feautres__title {
    padding: 0 40px;

    @media (min-width: 414px) {
        line-height: 63px;
    }

    @media (min-width: 576px) {
        padding: 0;
    }
}

.features__descr {
    margin-top: 10px;

    font-weight: 500;
    font-size: 14px;
    line-height: 20px;

    text-align: center;
    letter-spacing: 0.2px;

    color: #737373;
}

.features__cards {
    margin-top: 75px;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media (min-width: 414px) {
        flex-direction: row;
        flex-wrap: wrap;
        align-items: baseline;
        justify-content: space-evenly;
    }

    @media (min-width: 992px) {
        flex-wrap: nowrap;
        justify-content: space-around;
        align-items: flex-start;

        margin-top: 45px;
    }

    @media (min-width: 1150px) {
        justify-content: center;
    }
}

.features__cards--reverse {
    @media (min-width: 900px) {
        justify-content: center;
    }
}