.container {
    max-width: 253px;
    width: 100%;
    margin: 0 auto;

    @media (min-width: 414px) {
        max-width: 328px;
    }

    @media (min-width: 415px) {
        max-width: 72.85%;
    }

    @media (min-width: 1350px) {
        max-width: 1046px;
    }
}

.container--nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.container--clients {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;

    @media (min-width: 768px) {
        flex-direction: row;
        justify-content: space-between;
    }
}

.container--contact {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    @media (min-width: 768px) {
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
}

.container--links {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.container--social {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (min-width: 576px) {
        flex-direction: row;
    }
}