.card-item {
    max-width: 230px;
    width: 100%;
    padding: 35px 40px;
    margin-top: 30px;

    background: #fff;

    @media (min-width: 992px) {
        margin-right: 30px;
        margin-top: 0;

        padding: 15px 5px;
    }

}

.card-item--main {
    max-width: 328px;

    @media (min-width: 576px) {
        max-height: 210px;
    }

    @media (min-width: 992px) {
        margin-top: 15px;
        padding: 15px 18px;
        min-height: 230px;
        max-height: none;

        box-shadow: 0px 13px 19px 0px #00000012;

        &:last-child {
            margin-right: 0;
        }
    }

    @media (min-width: 1200px) {
        padding: 35px 40px;
    }
}

.card-item--reverse {
    padding: 0;
    padding-top: 30px;

    @media (min-width: 900px) {
        margin-right: 20px;
    }

    @media (min-width: 992px) {
        max-width: 230px;

        display: flex;
        flex-direction: column;
        justify-content: space-between;

        min-height: 341px;

        margin-top: 0;
    }

    @media (min-width: 1024px) {
        &:last-child {
            margin-right: 0;
        }
    }

    @media (min-width: 1350px) {
        margin-right: 40px;
    }
}

.card-item--blue {
    background: $secondaryColor;

    .card-item__title,
    .card-item__text {
        color: white;
    }
}

.card-item--feature {
    .card-item__img-wrapper {
        text-align: center;
    }

    &:last-child {
        margin-right: 0;
    }

    @media (min-width: 1150px) {
        height: 209px;
    }

    @media (min-width: 1200px) {
        padding: 30px 32.5px;
    }

    @media (min-width: 1350px) {
        margin-right: 38px;
    }
}

.card-item__title {
    margin-top: 11px;

    font-weight: bold;
    font-size: 24px;
    letter-spacing: 0.1px;

    color: $text;
}

.card-item__title--reverse {
    font-size: 16px;
    margin-top: 0;

    @media (min-width: 414px) {
        line-height: 24px;
    }
}

.card-item__text {
    margin-top: 11px;

    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: $secondText;

}

.card-item__text--reverse {
    margin-top: 15px;
}

.card-item__text-wrapper {
    text-align: center;
}

.card-item__text-wrapper--reverse {
    text-align: left;
    padding-left: 29px;

    @media (min-width: 992px) {
        padding-left: 15px;
    }
}

.card-item__number {
    font-weight: bold;
    font-size: 40px;
    line-height: 57px;

    letter-spacing: 0.2px;

    color: $darkBackground;

    @media (min-width: 414px) {
        margin-top: 10px;
    }

    @media (min-width: 1200px) {
        line-height: 42px;
    }
}

.card-item__descr {
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.1px;

    color: $secondText;

    @media (min-width: 414px) {
        line-height: initial;
        margin-top: 10px;
    }
}

.card-item__img-wrapper--reverse {
    margin-top: 30px;
}

.card-item__img {
    max-width: 100%;
    width: 100%;
    height: 100%;
    
    object-fit: cover;

    @media (min-width: 414px) {
        display: block;
    }
}