.about {
    padding-top: 100px;
    background: $lightBackground;

    @media (min-width: 992px) {
        position: relative;
        padding-bottom: 77px;
    }
}

.about__bottom-panel {
    @media (min-width: 992px) {
        position: absolute;
        bottom: 0;
    
        height: 77px;
        width: 100%;
    
        background: #252B42;
    }
}

.about__main-title {
    text-transform: none;
}

.about__title {
    text-align: left;
    text-transform: initial;

    @media (min-width: 992px) {
        padding-top: 20px;
    }
}

.about__text {
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    letter-spacing: 0.2px;
    color: $secondText;

    margin-top: 10px;

    br {
        display: none;
    }
    
    @media (min-width: 414px) {
        text-align: left;
        line-height: 20px;
    }

    @media (min-width: 576px) {
        text-align: center;
    }

    @media (min-width: 1024px) {
        br {
            display: block;
        }
    }
}

.about__text--side {
    @media (min-width: 576px) {
        text-align: left;
    }
}

.about__main {

    @media (min-width: 992px) {
        display: flex;
        justify-content: space-between;

        margin-top: 100px;
    }

    @media (min-width: 1350px) {
        justify-content: center;
    }

}

.about__video {
    position: relative;
    overflow: hidden;

    margin-top: 100px;

    height: 442px;
    width: 100%;

    video {
        width: 100%;
        height: 100%;
        max-width: 100%;
        object-fit: cover;
        background: linear-gradient(to bottom, rgb(42, 42, 42, 0.3), rgb(42, 42, 42, 0.3));
    }

    @media (min-width: 992px) {
        max-width: 597px;
        margin-top: 0;
        margin-right: 20px;
    }

    @media (min-width: 1350px) {
        margin-right: 100px;
    }
}


.player-icon {
    fill: #ffffff;
    width: 24px;
    height: 24px;
}

.hide {
    display: none;
}

.about__filter {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 14.58%, rgba(56, 56, 56, 0.84) 100%);
}

.about__side {
    margin-top: 72px;

    @media (min-width: 992px) {
        margin-top: 0;
        max-width: 324px;
    }
}

.about__text-cards {
    margin-top: 50px;
}

.about__icon {
    width: 100%;
}

.about__clients {
    padding: 20px 0 85px;
    margin-top: 100px;

    background: $grayBackground;

    @media (min-width: 992px) {
        padding-bottom: 137px;
    }
}

.about__client {
    margin-top: 60px;

    @media (min-width: 414px) {
        width: 105px;
    }

    @media (min-width: 768px) {
        width: 75px;
    }

    @media (min-width: 992px) {
        width: 90px;
    }

    @media (min-width: 1350px) {
        width: 102px;
    }
}