.contact {
    padding: 40px 0;
}

.contact__title {
    font-weight: bold;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: 0.1px;
    color: $text;

    @media (min-width: 768px) {
        line-height: initial;
    }
}

.contact__descr {
    margin-top: 10px;

    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: $secondText;

    @media (min-width: 414px) {
        padding-right: 96px;
    }

    @media (min-width: 768px) {
        margin-top: 0;
        padding-right: 0;
    }

    @media (min-width: 992px) {
        margin-top: 10px;
    }
}

.contact__text-wrapper {
    padding-left: 17px;
    
    @media (min-width: 768px) {
        padding-left: 0;
    }
}