.links {
    padding: 55.5px 0;
    background: $thirdaryColor;

    @media (min-width: 992px) {
        padding: 50px 0;
    }
}

.links__list {
    margin-top: 30px;
    
    @media (min-width: 992px) {
        margin-right: 40px;
    }

    @media (min-width: 1350px) {
        margin-right: 63px;
    }
}

.links__wrapper {
    padding-left: 17px;

    @media (min-width: 992px) {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
    }
}

.links__item {
    a {
        display: flex;
        align-items: center;
        margin-bottom: 10px;

        font-weight: 600;
        font-size: 14px;
        letter-spacing: 0.2px;
        color: $lightText;

        transition: color 0.2s;

        &:hover,
        &:focus {
            color: darken($color: $lightText, $amount: 12%);
        }
    }
}

.links__item--header {
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 0.1px;
}

.links__item--first {
    margin-top: 20px;
}

.links__item--contact {
    display: flex;
}

.links__icon-wrapper {
    display: inline-block;
    width: 22px;
    height: 22px;
    flex-shrink: 0;

    margin-right: 10px;
}

.links__icon {
    width: 100%;
    height: 100%;

    min-width: 100%;
}