.btn {
    font-weight: bold;
    font-size: 14px;
    text-align: center;
    letter-spacing: 0.2px;

    padding: 16px 36px;
    border-radius: 37px;

    transition: background-color 0.2s ease-in,
                color 0.2s ease-in;


    cursor: pointer;
}

.btn__quote {
    width: 185px;
    background: $primaryColor;
    color: $lightText;

    @media (min-width: 992px) {
        width: initial;
        margin-right: 10px;

        line-height: 28px;
    }

    &:hover,
    &:focus {
        background-color: darken($color: $primaryColor, $amount: 8%);
    }

    &:active {
        color: darken($color: $lightText, $amount: 10%);
        background-color: darken($color: $primaryColor, $amount: 10%);
    }   
}

.btn__more {
    width: 156px;

    padding: 14px 36px;
    margin-top: 25px;

    background: transparent;
    border: 2px solid $secondaryColor;

    color: $secondaryColor;


    @media (min-width: 992px) {
        width: initial;
        margin-top: 0;

        line-height: 28px;
    }

    &:hover,
    &:focus {
        background-color: $secondaryColor;
        color: $lightText;
    }

    &:active {
        color: darken($color: $lightText, $amount: 10%);
        background-color: darken($color: $secondaryColor, $amount: 10%);
    }
}

.btn__sub {
    padding: 15px 2px;

    font-size: 14px;
    word-break: break-all;
    text-align: center;
    letter-spacing: 0.2px;
    color: $lightText;

    background: $primaryColor;

    border: 1px solid #E6E6E6;
    border-radius: 0;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;

    @media (min-width: 414px) {
        padding: 15px 22.5px;
        word-break: initial;
    }

    &:hover,
    &:focus {
        background: darken($color: $primaryColor, $amount: 5%);
        color: $lightText;
    }

    &:active {
        background: darken($color: $primaryColor, $amount: 10%);
        color: darken($color: $lightText, $amount: 5%);
    }
}

.btn__in-touch {
    width: 160px;
    margin-top: 15px;
    padding: 10px 20px;

    font-weight: 600;
    font-size: 14px;
    text-align: center;
    letter-spacing: 0.2px;
    color: $primaryColor;

    border: 1px solid $primaryColor;
    border-radius: 5px;
    background: transparent;



    @media (min-width: 414px) {
        width: initial;
        line-height: 24px;
    }

    &:hover,
    &:focus {
        background: $primaryColor;
        color: $lightText;
    }

    &:active {
        background: darken($color: $primaryColor, $amount: 5%);
        color: darken($color: $lightText, $amount: 5%);
    }
}

.btn__contact {
    margin-top: 30px;
    margin-left: 17px;
    padding: 15px 40px;
    
    font-weight: bold;
    font-size: 14px;
    text-align: center;
    letter-spacing: 0.2px;
    color: $lightText;

    background: $primaryColor;
    border-radius: 5px;

    @media (min-width: 414px) {
        width: initial;
        line-height: 28px;
    }

    @media (min-width: 768px) {
        margin-top: 0;
        margin-left: 0;
        line-height: initial;
    }

    &:hover,
    &:focus {
        background: darken($color: $primaryColor, $amount: 5%);
    }

    &:active {
        background: darken($color: $primaryColor, $amount: 10%);
        color: darken($color: $lightText, $amount: 5%);
    }
}

.btn__about {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    background: transparent;

    display: flex;
    align-items: center;
    justify-content: center;
    width: 98px;
    height: 98px;

    border-radius: 50%;
}

.btn__about--overlay {
    background: $secondaryColor;

    &:hover,
    &:focus {
        background: darken($color: $secondaryColor, $amount: 3%);
    }

    &:active {
        background: darken($color: $secondaryColor, $amount: 7%);
    }
}