.in-touch {
    padding-top: 75px;
    padding-bottom: 130px;
    background: $darkGrayBackground;
}

.in-touch__title {
    text-transform: initial;
}

.in-touch__descr {
    margin-top: 10px;

    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.2px;
    color: $secondText;

    br {
        display: none;
    }

    @media (min-width: 414px) {
        line-height: 24px;
        padding: 0 18.5px;
    }

    @media (min-width: 1024px) {
        br {
            display: block;
        }
    }
}

.in-touch__cards {
    margin-top: 60px;

    display: flex;
    flex-direction: column;
    align-items: center;

    @media (min-width: 992px) {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

    @media (min-width: 1024px) {
        flex-wrap: nowrap;
    }
}