.subscribe {
    padding: 30px 38.5px;

    background: $thirdaryColor;

    br {
        display: none;
    }
    
    @media (min-width: 768px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    @media (min-width: 992px) {
        position: absolute;
        left: 50%;
        bottom: 20px;
        z-index: 1000;

        transform: translateX(-50%);

        max-width: 986px;
        width: 100%;

        br {
            display: block;
        }
    }
}

.subscribe__title {
    font-weight: bold;
    font-size: 24px;
    text-align: center;
    letter-spacing: 0.1px;

    color: $lightText;

    @media (min-width: 768px) {
        text-align: left;
    }
}

.subscribe__form {
    margin-top: 30px;

    display: flex;

    @media (min-width: 768px) {
        max-width: 450px;
        width: 100%;
        margin-top: 0;
    }
}


.subscribe__input {
    width: 100%;
    min-width: 164px;
    padding: 17.5px 0 17.5px 20px;

    font-size: 14px;
    letter-spacing: 0.2px;
    color: $text;

    background: #F9F9F9;
    border: 1px solid #E6E6E6;
    border-radius: 0;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;

}

.subscribe__input::placeholder {
    font-size: 14px;
    letter-spacing: 0.2px;
    color: $secondText;
}