.promo {
    padding-top: 84px;
    padding-bottom: 205px;
    
    @media (min-width: 320px) {
        padding-bottom: 205px;
    }

    @media (min-width: 992px) {
        padding-bottom: 325px;
        margin-top: 0;
    }

    @media (min-width: 1200px) {
        padding-bottom: 293px;
    }
}

.promo__title {

    @media (min-width: 992px) {
        text-align: left;
    }

    @media (min-width: 1350px) {
        font-size: 58px;
        line-height: 80px;
    }
}

.promo__text {
    margin-top: 30px;
    font-weight: 500;
    font-size: 20px;

    text-align: center;
    letter-spacing: 0.2px;

    color: $secondText;

    @media (min-width: 414px) {
        margin-top: 40px;
    }

    @media (min-width: 992px) {
        text-align: left;
        margin-top: 35px;
    }
}

.promo__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;

    @media (min-width: 992px) {
        flex-direction: row;
    }
}

.promo__cards {
    margin-top: 80px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media (min-width: 992px) {
        position: absolute;
        left: 50%;
        bottom: -20px;

        transform: translate(-50%);

        width: 73%;

        flex-direction: row;
        flex-wrap: nowrap;
        justify-content: center;
        align-items: baseline;
    }
}