.card-contact {
    padding: 40px 15px;
    margin-top: 30px;
    max-width: 328px;

    color: $text;

    background: $lightBackground;
    
    @media (min-width: 576px) {
        padding: 50px 53.5px;
    }
    
    @media (min-width: 992px) {
        margin-right: 20px;
    }

    @media (min-width: 1024px) {
        padding: 40px 32px;
        margin-right: 0;
    }

    @media (min-width: 1150px) {
        padding: 50px 45px;
    }

    @media (min-width: 1200px) {
        padding: 50px 53.5px;
    }

}

.card-contact--dark {
    color: $lightText;

    background: $darkBackground;

    .card-contact__title {
        color: $lightText;
    }

    .card-contact__email {
        color: $lightText;
    }

    .btn__in-touch {
        color: $lightText;
        border: 1px solid $lightText;

        transition: background 0.2s ease,
                    color 0.2s ease,
                    border 0.2s ease;

        &:hover,
        &:focus {
            background: $lightBackground;
            border-color: $lightBackground;
            color: $darkBackground;
        }

        &:active {
            background: darken($color: $lightBackground, $amount: 8%);
            color: darken($color: $darkBackground, $amount: 5%);
        }
    }

    @media (min-width: 1024px) {
        padding: 80px 45px;
    }

    @media (min-width: 1200px) {
        padding: 80px 53.5px;
    }
}

.card-contact__content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card-contact__img-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 72px;
    height: 72px;
}

.card-contact__email {
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.2px;
    color: $text;

    transition: background-color 0.2s,
                color 0.2s,
                opacity 0.2s;

    &--first {
        margin-top: 14px;
    }

    &:hover,
    &:focus {
        opacity: 0.8;
    }

    &:active {
        opacity: 0.6;
    }
}

.card-contact__title {
    margin-top: 15px;

    font-weight: bold;
    font-size: 16px;

    text-align: center;
    letter-spacing: 0.1px;


    color: $text;
}