.social {
    padding-top: 25px;
    padding-bottom: 55px;

    @media (min-width: 768px) {
        padding: 25px 0;
    }
}

.social__rights {
    font-weight: 600;
    font-size: 14px;
    padding-left: 17px;
    letter-spacing: 0.2px;
    color: $secondText;

    @media (min-width: 414px) {
        line-height: 24px;
        padding-left: 30px;
        padding-right: 57px;
    }

    @media (min-width: 768px) {
        padding: 0;
        line-height: initial;
    }
}

.social__links {
    display: flex;
    justify-content: space-evenly;
    width: 236px;
    margin-top: 40px;

    @media (min-width: 576px) {
        margin-top: 25px;
    }

    @media (min-width: 768px) {
        margin-top: 0;
    }
}

.social__link {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 24px;
    height: 24px;
    
    transition: transform 0.3s;

    &:hover,
    &:focus {
        transform: rotate(12deg);
    }
}

